import { notFound } from "next/navigation";
import ProjectDetailPage from "./client";

interface Project {
  _id: string;
  projectName: string;
  slug: string;
  category: string;
  reraNumber: string;
  longDescription: string;
  shortDescription: string;
  projectLocation: string | {
    _id: string;
    name: string;
    city: string;
    state: string;
    country: string;
  };
  status: string;
  area: string;
  type: string;
  heading: string;
  projectLogo: string;
  projectThumbnail: string;
  locationImage: string;
  locationAltText: string;
  locationType: string;
  locationIframe: string;
  isActive: boolean;
  bannerVideo: {
    mobile: string;
    desktop: string;
    tablet: string;
  };
  bannerVideoPoster: {
    mobile: string;
    desktop: string;
    tablet: string;
  };
  sitePlanImage: {
    original: string;
    mobile: string;
    desktop: string;
    altText: string;
  };
  galleryImages: Array<{
    original: string;
    mobile: string;
    desktop: string;
    altText: string;
  }>;
  nearbyLocations: Array<{
    name: string;
    distance: string;
    unit: string;
    image: string;
    altText: string;
  }>;
  floorPlans: Array<{
    image: string;
    altText: string;
    planType: string;
    area: string;
    unit: string;
  }>;
  faqs: Array<{
    question: string;
    answer: string;
  }>;
  amenities: string[] | Array<{
    _id: string;
    heading: string;
    subHeading: string;
    image: string;
    caption: string;
  }>;
  createdAt: string;
  updatedAt: string;
}


interface ProjectPageProps {
  params: Promise<{ slug: string }>;
}

export default async function ProjectPage({ params }: ProjectPageProps) {
  const { slug } = await params;
  const res = await fetch(`https://suncityprojects.com/api/projects/${slug}`, {
    cache: 'no-store',
  });
  const project = await res.json();

  if (!project) {
    notFound();
  }

  return (
    <ProjectDetailPage project={project.data} />
  );
}